const PACKAGE_NAME = '@wix/auto_sdk_benefit-programs_transactions';

/** Retrieves a transaction. */
export function getTransaction(payload: object): RequestOptionsFactory<any> {
  function __getTransaction({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.benefit_programs.v1.transaction',
      method: 'GET' as any,
      methodFqn:
        'wix.benefit_programs.v1.transaction.TransactionService.GetTransaction',
      packageName: PACKAGE_NAME,
      url: resolveWixBenefitProgramsV1TransactionTransactionServiceUrl({
        protoPath: '/v1/transactions/{transactionId}',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'transaction.createdDate' },
              { path: 'transaction.updatedDate' },
              { path: 'transaction.details.effectiveDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __getTransaction;
}

/**
 * Creates a query to retrieve a list of transactions.
 *
 * The Query Transactions method builds a query to retrieve a list of transactions and returns a `TransactionsQueryBuilder` object.
 *
 * The returned object contains the query definition, which is used to run the query using the `find()` method.
 *
 * You can refine the query by chaining `TransactionsQueryBuilder` methods onto the query. `TransactionsQueryBuilder` methods enable you to filter, sort, and control the results that Query Transactions returns.
 *
 * Query Transactions has a default paging limit of 50, which you can override.
 *
 *  For a full description of the item object, see the object returned for the `items` property in `TransactionsQueryResult`.
 */
export function queryTransactions(payload: object): RequestOptionsFactory<any> {
  function __queryTransactions({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.benefit_programs.v1.transaction',
      method: 'POST' as any,
      methodFqn:
        'wix.benefit_programs.v1.transaction.TransactionService.QueryTransactions',
      packageName: PACKAGE_NAME,
      url: resolveWixBenefitProgramsV1TransactionTransactionServiceUrl({
        protoPath: '/v1/transactions/query',
        data: payload,
        host,
      }),
      data: payload,
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [
              { path: 'transactions.createdDate' },
              { path: 'transactions.updatedDate' },
              { path: 'transactions.details.effectiveDate' },
            ],
          },
        ]),
    };

    return metadata;
  }

  return __queryTransactions;
}
